import { _decorator, Component, Prefab, Node, Label } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Animation } from 'cc';
import { ESTATE, iceAndFire } from './cIceAndFire';
import { EMOD } from './iceAndFireBg';
import { iceAndFireSymbol } from './iceAndFireSymbol';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { iceAndFireNormalReel } from './iceAndFireNormalReel';
import { iceAndFireFortuneReel } from './iceAndFireFortuneReel';
import { director } from 'cc';
import { ApiReturn } from 'tsrpc-browser';
import { ResPlaySlots } from '../../module_basic/shared/protocols/public/slots/PtlPlaySlots';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { ResPlayMatch3 } from '../../module_basic/shared/protocols/public/slots/PtlPlayMatch3';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { dataChange, reelInfo } from './dataChange';
import { iceAndFireBigwin } from './iceAndFireBigwin';
import { instantiate } from 'cc';
import { bezierMove } from '../../module_basic/utils/bezierMove';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
import { Button } from 'cc';
import { iceAndFireDetail } from './iceAndFireDetail';
import { iceAndFireAudio } from './iceAndFireAudio';
import { labNumShow } from '../../module_basic/utils/labNumShow';
import { EState } from './iceAndFireInfoBoard';
import { Tween } from 'cc';
import { iceAndFireSlotCtrl } from './iceAndFireSlotCtrl';
import { Num } from '../../core_tgx/framework/util/Num';
import { fromEuler } from '../../../extensions/plugin-import-2x/creator/common/utlis';
const { ccclass, property } = _decorator;

@ccclass('iceAndFireMain')
export class iceAndFireMain extends Component {
    @property({ type: Prefab, tooltip: "" })
    public detailPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public coinNumPb: Prefab = null!;
    @property({ type: iceAndFireNormalReel, tooltip: "" })
    public normalReel: iceAndFireNormalReel = null!;
    @property({ type: iceAndFireFortuneReel, tooltip: "" })
    public topReel: iceAndFireFortuneReel = null!;
    @property({ type: iceAndFireFortuneReel, tooltip: "" })
    public downReel: iceAndFireFortuneReel = null!;
    @property({ type: Prefab, tooltip: "" })
    public bigwinPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public mulPb: Prefab = null!;

    public delayClick: boolean = false;

    onLoad() {

    }

    start() {
        iceAndFire.main = this;
        iceAndFire.audio = this.getComponent(iceAndFireAudio);
        iceAndFire.normalReel = this.normalReel;
        iceAndFire.topReel = this.topReel;
        iceAndFire.downReel = this.downReel;
    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                this.add();
                break;
            case "dec":
                this.dec();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                break;
            case "lastmoney":
                iceAndFire.lastmoneyPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                iceAndFire.clear();
                RoomMgr.inst.exitRoom();
            }
        });
    }

    startRoll() {
        if (this.delayClick || iceAndFire.rollEnable == false) {
            return;
        }
        if (!iceAndFire.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (iceAndFire.status == ESTATE.stop) {
                iceAndFire.status = ESTATE.start;
                this.sendRoll();
            } else if (iceAndFire.status == ESTATE.run) {
                iceAndFire.slotCtrl.setSpinAnim(3);
                this.stopImmediately(iceAndFire.roundData.reelInfo[0].symbols, () => { this.rollCallBack() });
            }
        }
    }

    add() {
        if (iceAndFire.auto) {
            return;
        }
        iceAndFire.sumIdx += 1;
        iceAndFire.sumIdx = iceAndFire.slotCtrl.updateBet(iceAndFire.sumIdx) == 0 ? iceAndFire.sumIdx - 1 : iceAndFire.sumIdx;
    }

    dec() {
        if (iceAndFire.auto) {
            return;
        }
        iceAndFire.sumIdx -= 1;
        iceAndFire.sumIdx = iceAndFire.slotCtrl.updateBet(iceAndFire.sumIdx) == 0 ? iceAndFire.sumIdx + 1 : iceAndFire.sumIdx;
    }

    startAuto(n: any) {
        if (this.delayClick) {
            return;
        }
        if (iceAndFire.status == ESTATE.stop) {
            iceAndFire.auto = true;
            iceAndFire.autoTimes = n;
            (((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            // (((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[1] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
                ((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
                ((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
                ((iceAndFire.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(1, 1, 1);
            }
            iceAndFire.slotCtrl.Btn_stopAuto.active = iceAndFire.auto;
            iceAndFire.slotCtrl.Btn_start.active = !iceAndFire.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1.5);
        }
    }

    stopAuto() {
        iceAndFire.auto = false;
        iceAndFire.autoTimes = 0;
    }

    private rollCallBackBn: boolean = false;
    public rollCallBack() {
        if (this.rollCallBackBn) {
            return;
        }
        this.rollCallBackBn = true;
        this.scheduleOnce(() => {
            this.rollCallBackBn = false;
        }, 1)
        if (iceAndFire.roundIndex == 0) {
            iceAndFire.normalReel.removeRadom();
        } else {
            iceAndFire.topReel.removeRadom();
            iceAndFire.downReel.removeRadom();
        }
        if (iceAndFire.roundWin > 0) {
            iceAndFire.slotCtrl.setSpinAnim(3);
        } else {
            iceAndFire.slotCtrl.setSpinAnim(2);
        }
        this.scheduleOnce(() => {
            this.rollCallBackNext();
        }, 0.3);
    }

    /**滚动完成后操作 */
    private rollCallBackNext() {
        let t: number = 0.5;
        if (iceAndFire.isWinning) {
            let currentMoney = iceAndFire.money;
            let targetMoney = iceAndFire.lotteryRes.userscore;
            let moneyObj = { value: currentMoney };
            iceAndFire.moneyTween = tween(moneyObj)
                .to(0.5, { value: targetMoney }, {
                    onUpdate: () => {
                        // 实时更新数据
                        iceAndFire.money = moneyObj.value;
                        iceAndFire.slotCtrl.moneyLab.string = Num.exchangeToThousands(
                            iceAndFire.exchangeRate,
                            moneyObj.value
                        );
                    }
                })
                .start();
        }
        if (iceAndFire.roundIndex == 0) {
            let reelInfo: reelInfo = iceAndFire.roundData.reelInfo[0];
            if (reelInfo.removeItems.length > 0) {
                t += 2;
            }
            iceAndFire.normalReel.showWin(reelInfo.removeItems);
            let mul: number = reelInfo.multiplier ? reelInfo.multiplier : 1;
            if (reelInfo.payout > 0) iceAndFire.infoBoard.playWinAni(reelInfo.payout / mul);
            if (mul > 1) {
                t += 1;
                iceAndFire.normalReel.showWinWildEffecct();
                let mulNode: Node = instantiate(this.mulPb);
                mulNode.getComponent(Label).string = "X2";
                mulNode.scale = new Vec3(1.5, 1.5);
                mulNode.parent = this.node;
                mulNode.opacity = 0;
                mulNode.position = iceAndFire.normalReel.node.position;
                let bz: bezierMove = mulNode.getComponent(bezierMove);
                let p0: Vec3 = mulNode.position;
                let p3: Vec3 = iceAndFire.infoBoard.node.position;
                let p1: Vec3 = new Vec3(250, (p0.y + p3.y) / 4);
                let p2: Vec3 = new Vec3(250, p3.y);
                bz.pArr = [p0, p1, p2, p3];
                tween(mulNode)
                    .to(0.5, { opacity: 255, scale: new Vec3(1, 1) })
                    .delay(0.1)
                    .call(() => {
                        iceAndFire.audio.playFly();
                        tween(bz)
                            .to(0.5, { t: 1 })
                            .call(() => {
                                mulNode.destroy();
                                this.settlement();
                            })
                            .start();
                    })
                    .start();
            }
        } else {
            let topReelInfo: reelInfo = iceAndFire.roundData.reelInfo[0];
            let downReelInfo: reelInfo = iceAndFire.roundData.reelInfo[1];
            if (topReelInfo.removeItems.length > 0 || downReelInfo.removeItems.length > 0) {
                t += 2;
            }
            iceAndFire.topReel.showWin(iceAndFire.roundData.reelInfo[0].removeItems);
            iceAndFire.downReel.showWin(iceAndFire.roundData.reelInfo[1].removeItems);
            this.playMul(true);
            this.playMul(false);
        }


        this.scheduleOnce(this.doNext, t);
    }

    private playMul(topBn: boolean) {
        let reelInfo: reelInfo;
        let reel: iceAndFireFortuneReel;
        if (topBn) {
            reelInfo = iceAndFire.roundData.reelInfo[0];
            reel = iceAndFire.topReel;
        } else {
            reelInfo = iceAndFire.roundData.reelInfo[1];
            reel = iceAndFire.downReel;
        }
        let reelPos: Vec3 = reel.node.parent.parent.position;
        let mul: number = reelInfo.multiplier ? reelInfo.multiplier : 1;
        let moveX: number = topBn ? -250 : 250;
        let coinNum: labNumShow;
        if (reelInfo.payout) {
            reel.showWinWildEffecct();
            coinNum = instantiate(this.coinNumPb).getComponent(labNumShow);
            coinNum.node.parent = this.node;
            coinNum.node.setSiblingIndex(this.node.children.length - 2);
            coinNum.node.position = reelPos;
            coinNum.setRealNum(reelInfo.payout / mul);
        }
        if (mul == 1) {
            if (reelInfo.payout) this.coinTween(coinNum, reelInfo.payout, moveX);
            return;
        }
        if (mul == 2) {
            this.scheduleOnce(() => {
                let mulNode: Node = instantiate(this.mulPb);
                mulNode.getComponent(Label).string = "X2";
                mulNode.scale = new Vec3(1.2, 1.2);
                mulNode.parent = this.node;
                mulNode.setSiblingIndex(this.node.children.length - 2);
                mulNode.opacity = 0;
                mulNode.position = reelPos;
                tween(mulNode)
                    .to(0.5, { opacity: 255, scale: new Vec3(1, 1) })
                    .call(() => {
                        this.coinTween(coinNum, reelInfo.payout, moveX);
                    })
                    .to(0.3, { opacity: 0 })
                    .call(() => {
                        mulNode.destroy();
                    })
                    .start();
            }, 0.5);
        }
        if (mul == 5) {
            this.scheduleOnce(() => {
                let mulNode: Node = instantiate(this.mulPb);
                mulNode.getComponent(Label).string = "X5";
                mulNode.parent = this.node;
                mulNode.setSiblingIndex(this.node.children.length - 2);
                mulNode.position = iceAndFire.infoBoard.node.position;
                let bz: bezierMove = mulNode.getComponent(bezierMove);
                let p0: Vec3 = mulNode.position;
                let p3: Vec3 = reelPos;
                let p1: Vec3 = new Vec3(moveX, p0.y);
                let p2: Vec3 = new Vec3(moveX, p3.y);
                bz.pArr = [p0, p1, p2, p3];
                tween(bz)
                    .to(0.5, { t: 1 })
                    .call(() => {
                        this.coinTween(coinNum, reelInfo.payout, moveX);
                        tween(mulNode)
                            .to(0.3, { opacity: 0 })
                            .call(() => {
                                mulNode.destroy();
                            })
                            .start();
                    })
                    .start();
            }, 0.5);
        }
        if (mul == 10) {
            this.scheduleOnce(() => {
                let mulNode: Node = instantiate(this.mulPb);
                mulNode.getComponent(Label).string = "X5";
                mulNode.parent = this.node;
                mulNode.setSiblingIndex(this.node.children.length - 2);
                mulNode.position = iceAndFire.infoBoard.node.position;
                let bz: bezierMove = mulNode.getComponent(bezierMove);
                let p0: Vec3 = mulNode.position;
                let p3: Vec3 = reelPos;
                let p1: Vec3 = new Vec3(moveX, p0.y);
                let p2: Vec3 = new Vec3(moveX, p3.y);
                bz.pArr = [p0, p1, p2, p3];
                tween(bz)
                    .to(0.5, { t: 1 })
                    .delay(0.3)
                    .call(() => {
                        mulNode.destroy();
                        mulNode = instantiate(this.mulPb);
                        mulNode.getComponent(Label).string = "X10";
                        mulNode.scale = new Vec3(1.2, 1.2);
                        mulNode.parent = this.node;
                        mulNode.setSiblingIndex(this.node.children.length - 2);
                        mulNode.opacity = 0;
                        mulNode.position = reelPos;
                        tween(mulNode)
                            .to(0.5, { opacity: 255, scale: new Vec3(1, 1) })
                            .call(() => {
                                this.coinTween(coinNum, reelInfo.payout, moveX);
                            })
                            .to(0.3, { opacity: 0 })
                            .call(() => {
                                mulNode.destroy();
                            })
                            .start();
                    })
                    .start();
            }, 0.5);
        }
    }

    private coinTween(coinNum: labNumShow, payout: number, moveX: number) {
        tween(coinNum)
            .to(0.5, { num: payout * 100 })
            .call(() => {
                this.moveToInfoBoard(coinNum.node.getComponent(bezierMove), moveX);
            })
            .start();
    }

    private moveToInfoBoard(bz: bezierMove, moveX: number) {
        let p0: Vec3 = bz.node.position;
        let p3: Vec3 = iceAndFire.infoBoard.node.position;
        let p1: Vec3 = new Vec3(moveX, p0.y);
        let p2: Vec3 = new Vec3(moveX, p3.y);
        bz.pArr = [p0, p1, p2, p3];
        tween(bz)
            .to(0.5, { t: 1 })
            .call(() => {
                this.settlement();
                bz.node.destroy();
            })
            .start();
    }

    private showStartBn: boolean = false;
    doNext() {
        this.unschedule(this.doNext);
        iceAndFire.topReel.unscheduleAllCallbacks();
        iceAndFire.downReel.unscheduleAllCallbacks();
        if (iceAndFire.freeBn && iceAndFire.roundIndex == 0 && this.showStartBn == false) {
            this.showStartBn = true;
            iceAndFire.slotCtrl.showFortuneStart();
            return;
        }
        iceAndFire.roundIndex++;
        iceAndFire.infoBoard.state = EState.info;
        let len: number = iceAndFire.lotteryRes.rounds.length;
        if (iceAndFire.roundIndex < len) {
            iceAndFire.slotCtrl.lastNum = iceAndFire.roundData.leftFreeCount - 1;
            iceAndFire.topReel.loop();
            iceAndFire.downReel.loop();
            this.scheduleOnce(() => {
                iceAndFire.topReel.loopEnd(iceAndFire.roundData.reelInfo[0].symbols);
                iceAndFire.downReel.loopEnd(iceAndFire.roundData.reelInfo[1].symbols);
            }, 1.5);
        } else {
            if (iceAndFire.freeBn) {
                iceAndFire.slotCtrl.showFortuneEnd(len - 1, iceAndFire.lotteryRes.winscore);
            } else {
                this.playRollOver();
            }
        }
    }

    /**结算 */
    private settlementBn: boolean = false;
    settlement() {
        if (this.settlementBn) return;
        this.settlementBn = true;
        this.scheduleOnce(() => {
            this.settlementBn = false;
        }, 5)
        this.unschedule(this.doNext);
        if (iceAndFire.roundWin > iceAndFire.betSum * 5) {
            let bigwin: iceAndFireBigwin = instantiate(this.bigwinPb).getComponent(iceAndFireBigwin);
            bigwin.node.parent = this.node;
            bigwin.playBigWin(iceAndFire.roundWin);
        } else {
            iceAndFire.infoBoard.playWinAni(iceAndFire.roundWin, 1)
            this.scheduleOnce(() => {
                this.doNext();
            }, 1);
        }
    }

    closeBigWin() {
        iceAndFire.infoBoard.playWinAni(iceAndFire.roundWin)
        this.scheduleOnce(this.doNext, 1);
    }

    /**单次滚动完成结算 */
    playRollOver() {
        this.rollComplete();
        this.doAuto();
    }

    doAuto() {
        if (iceAndFire.auto && iceAndFire.autoTimes > 0) {
            this.sendRoll();
        }
    }
    /**滚动完成 */
    private rollComplete() {
        iceAndFire.status = ESTATE.stop;
        iceAndFire.slotCtrl.setSpinAnim(0);
        this.addDetailListener();
    }

    roll(list: number[]) {
        if (!iceAndFire.auto) {
            iceAndFire.rollEnable = true;
        }
        iceAndFire.status = ESTATE.run;
        this.scheduleOnce(() => {
            iceAndFire.normalReel.loopEnd(list, () => {
                this.rollCallBack();
            });
        }, 0.5);
    }

    sendRoll() {
        this.initRoll();
        iceAndFire.autoTimes = iceAndFire.autoTimes > 0 ? iceAndFire.autoTimes - 1 : 0;
        if (iceAndFire.autoTimes == 0) {
            iceAndFire.auto = false;
        }
        iceAndFire.slotCtrl.setSpinAnim(1);
        let autoTimesLab: Label = iceAndFire.slotCtrl.Btn_stopAuto.children[0].getComponent(Label) as Label;
        autoTimesLab.string = iceAndFire.autoTimes + "";
        // this.audio.playclickstart();
        if (iceAndFire.autoTimes > 999) {
            iceAndFire.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.5, 0.5);
        } else if (iceAndFire.autoTimes > 99) {
            iceAndFire.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.64, 0.64);
        }
        else {
            iceAndFire.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1);
        }
        iceAndFire.normalReel.loop();
        this.sendLottery();
    }

    firstSymbolOver() {
        iceAndFire.normalReel.spawn();
        this.addDetailListener();
    }

    private addDetailListener() {
        let symbols: baseSymbol[] = iceAndFire.normalReel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = true;
        }
    }

    private removeDetailListener() {
        let symbols: baseSymbol[] = iceAndFire.normalReel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.darkBn = false;
            symbol.node.getComponent(Button).interactable = false;
        }
        let detail: iceAndFireDetail = iceAndFire.detail;
        if (detail) detail.node.active = false;
    }

    addDetail(s: baseSymbol) {
        let detail: iceAndFireDetail = iceAndFire.detail;
        if (!detail) {
            detail = instantiate(this.detailPb).getComponent(iceAndFireDetail);
            detail.node.parent = s.node.parent.parent.parent;
            iceAndFire.detail = detail;
        }
        detail.node.active = true;
        let symbols: baseSymbol[] = iceAndFire.normalReel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.poolId != s.poolId) {
                symbol.darkBn = true;
            }
        }
        detail.node.position = s.node.position;
        detail.init(s as iceAndFireSymbol);
    }

    /**每次滚动初始化场景 */
    initRoll() {
        this.showStartBn = false;
        iceAndFire.normalReel.unscheduleAllCallbacks();
        iceAndFire.topReel.unscheduleAllCallbacks();
        iceAndFire.downReel.unscheduleAllCallbacks();
        iceAndFire.infoBoard.showInfo();
        this.removeDetailListener();
    }

    async sendLottery() {
        console.log("sendLottery");
        let retSpin: ApiReturn<ResPlayMatch3> = await gameNet.PlayMatch3(SubGameDef.SLOTS_ICEANDFIRE, iceAndFire.betSum + "");
        // if(retSpin.res)
        console.log(retSpin);
        this.lotteryBack(retSpin);
    }

    private lotteryBack(data: any) {
        if (data && data.isSucc) {
            this.exchange(data.res as ResPlayMatch3);
            if (iceAndFire.slotCtrl.isSpeed) {
                iceAndFire.roundIndex = 0;
                this.roll(iceAndFire.roundData.reelInfo[0].symbols)
            } else {
                this.scheduleOnce(() => {
                    iceAndFire.roundIndex = 0;
                    this.roll(iceAndFire.roundData.reelInfo[0].symbols)
                }, 1)
            }
        }
    }

    exchange(data: ResPlayMatch3) {
        let res = dataChange.change(data);
        iceAndFire.lotteryRes = res;
    }

    stopImmediately(arr: number[], callBack: Function = () => { }) {
        if (!iceAndFire.auto) {
            iceAndFire.normalReel.stopImmediately(arr, callBack)
        }
    }
}

