import { _decorator, Component, Prefab, Node, Label } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Animation } from 'cc';
import { ESTATE, iceAndFire } from './cIceAndFire';
import { find } from 'cc';
import { EMOD } from './iceAndFireBg';
const { ccclass, property } = _decorator;

@ccclass('fortuneStart')
export class fortuneStart extends Component {
    @property({ type: Animation, tooltip: "" })
    public ani: Animation = null!;
    @property({ type: Label, tooltip: "" })
    public freeLab: Label = null!;

    onLoad() {
    }

    start() {
       
    }

    show(){
        let freeCount:number = iceAndFire.roundData.reelInfo[0].freeCount;
        this.freeLab.string = freeCount+'';
        iceAndFire.slotCtrl.lastNum = freeCount;
        iceAndFire.bg.mod = EMOD.F;
        this.ani.play();
        this.scheduleOnce(this.remove,5);
    }

    remove(){
        iceAndFire.bg.cutScenes();
        this.node.destroy();
    }

    onClick(){
        this.remove();
    }
}

