import { NodePool } from "cc";
import { _decorator } from "cc";
import { Vec3 } from "cc";
import { ETYPE, reelDate } from "../../module_basic/ui_slot/match3/baseReel";
import { baseSymbol } from "../../module_basic/ui_slot/match3/baseSymbol";
import { iceAndFire } from "./cIceAndFire";
import { iceAndFireReel } from "./iceAndFireReel";
import { iceAndFireSymbol } from "./iceAndFireSymbol";


const { ccclass } = _decorator;
const FIRSTSYMBOL: number[] = [
    6, 4,
    3, 9, 5,
    7, 10, 1, 8,
    8, 1, 10, 7,
    5, 9, 3,
    4, 6
];
@ccclass('iceAndFireNormalReel')
export class iceAndFireNormalReel extends iceAndFireReel {
    onLoad() {

    }

    start() {
        this.radomIds = [1, 2, 3, 4, 5, 6, 7, 8, 9];
        this.setReelDate();
        // this.scheduleOnce(this.loop, 1);
        // this.scheduleOnce(() => {
        //     this.loopEnd(FIRSTSYMBOL);
        // }, 3);
    }

    /**设置滚轴排布数据 */
    setReelDate() {
        this.reelDates = [
            { index: 0, type: ETYPE.V, num: 2, pool: new NodePool(baseSymbol), size: 123, headPos: new Vec3(-307.5, -123), vector: 346, symbols: [], switch: false },
            { index: 1, type: ETYPE.V, num: 3, pool: new NodePool(baseSymbol), size: 123, headPos: new Vec3(-184.5, -184.5), vector: 469, symbols: [], switch: false },
            { index: 2, type: ETYPE.V, num: 4, pool: new NodePool(baseSymbol), size: 123, headPos: new Vec3(-61.5, -246), vector: 592, symbols: [], switch: false },
            { index: 3, type: ETYPE.V, num: 4, pool: new NodePool(baseSymbol), size: 123, headPos: new Vec3(61.5, -246), vector: 592, symbols: [], switch: false },
            { index: 4, type: ETYPE.V, num: 3, pool: new NodePool(baseSymbol), size: 123, headPos: new Vec3(184.5, -184.5), vector: 469, symbols: [], switch: false },
            { index: 5, type: ETYPE.V, num: 2, pool: new NodePool(baseSymbol), size: 123, headPos: new Vec3(307.5, -123), vector: 346, symbols: [], switch: false },
        ]
        this.bindResult(FIRSTSYMBOL);
        iceAndFire.main.firstSymbolOver();
    }

    private disTime: number = 0.1;
    /**启动循环播放 */
    loop() {
        iceAndFire.audio.playReelLoop();
        if (iceAndFire.slotCtrl.isSpeed) {
            this.v = 80;
        } else {
            this.v = 40;
        }
        let disTime: number = iceAndFire.slotCtrl.isSpeed ? 0 : this.disTime;
        this.reboundT = iceAndFire.slotCtrl.isSpeed ? 0 : 0.2;
        for (let i = 0; i < this.reelDates.length; i++) {
            this.scheduleOnce(() => {
                this.reboundMove(this.reelDates[i]);
            }, disTime * i);
        }
    }

    private reboundMove(r: reelDate): void {
        this.rebound(r, true, () => {
            this.moveOne(r);
        })
    }

    /**停止循环展示结果 */
    loopEnd(arr: number[], callback: Function = () => { }) {
        if (iceAndFire.slotCtrl.isSpeed) {
            this.v = 60;
        } else {
            this.v = 40;
        }
        if (iceAndFire.slotCtrl.isSpeed == false) {
            for (let i = 0; i < this.reelDates.length; i++) {
                let r: reelDate = this.reelDates[i];
                let disTime: number = this.disTime * i;
                this.scheduleOnce(() => {
                    this.bindResultOne(r, arr, () => {
                        let cb: Function = () => {
                            callback();
                            iceAndFire.audio.playReelEnd(false);
                        }
                        this.rebound(r, false, cb);
                    })
                }, disTime);
            }
        } else {
            this.bindResult(arr, () => {
                callback();
                iceAndFire.audio.playReelEnd(true);
            });
        }
    }

    spawn() {
        let symbols: iceAndFireSymbol[] = this.getSymbols as iceAndFireSymbol[];
        for (let i = 0; i < symbols.length; i++) {
            let symbol: iceAndFireSymbol = symbols[i];
            symbol.wildSpawn();
        }
    }
}

