import { NodePool } from "cc";
import { _decorator } from "cc";
import { Vec3 } from "cc";
import { baseReel, reelDate } from "../../module_basic/ui_slot/match3/baseReel";
import { baseSymbol } from "../../module_basic/ui_slot/match3/baseSymbol";
import { iceAndFireSymbol } from "./iceAndFireSymbol";
import { Node } from "cc";
import { instantiate } from "cc";
import { Logger } from "../../core_tgx/base/log/Logger";
import { tween } from "cc";
import { removeInfo } from "./dataChange";
import { macro } from "cc";


const { ccclass } = _decorator;

@ccclass('iceAndFireReel')
export class iceAndFireReel extends baseReel {
    /**wild effect 相关 */
    protected getWildEffect(): iceAndFireSymbol {
        let symbol: baseSymbol
        let pbNode = instantiate(this.symbolPb);
        symbol = pbNode.getComponent(baseSymbol);
        symbol.poolId = this.getPoolId();
        return symbol as iceAndFireSymbol;
    }

    /**X2wild表现 */
    showWinWildEffecct() {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            for (let j = r.symbols.length - 1; j >= 0; j--) {
                let symbol: iceAndFireSymbol = r.symbols[j] as iceAndFireSymbol;
                this.addWinWildEffecct(symbol);
            }
        }
    }
    private addWinWildEffecct(s: iceAndFireSymbol) {
        if (s.winBn && s.wildBn) {
            let symbol: iceAndFireSymbol = this.getWildEffect();
            symbol.node.parent = s.node.parent.parent.parent;
            symbol.node.position = s.node.position;
            symbol.node.scale = s.node.scale;
            symbol.symbolId = s.symbolId;
            symbol.winBn = true;
            // let scale: number = symbol.node.scale.x;
            tween(symbol.node)
                .to(0.4, { scale: new Vec3(1, 1) })
                .call(() => {
                    symbol.playWildWinEffect();
                })
                .delay(1)
                .to(0.4, { scale: new Vec3(0.7, 0.7) })
                .call(() => {
                    symbol.node.destroy();
                })
                .start();
        }
    }

    /**中奖表现 */
    showWin(rs: removeInfo[]) {
        this.removeUnuseAll();
        this.showCount = 0;
        this.rs = rs;
        this.doShow();
        this.schedule(this.doShow, 3, macro.REPEAT_FOREVER);
    }

    private showCount: number = 0
    private rs: removeInfo[];
    private doShow() {
        this.hideWin();
        let r: removeInfo = this.rs[this.showCount];
        let indexs: number[] = r.indexs;
        let symbols: baseSymbol[] = this.getSymbols;
        for (let i = 0; i < indexs.length; i++) {
            let symbol: iceAndFireSymbol = symbols[indexs[i]] as iceAndFireSymbol;
            symbol.winBn = true;
            if (r.winCoin && i == 0) {
                symbol.showWin(r.winCoin);
            }
        }

        if (this.showCount < this.rs.length - 1) {
            this.showCount++;
        } else {
            this.showCount = 0;
        }
    }

    hideWin() {
        let symbols: baseSymbol[] = this.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: iceAndFireSymbol = symbols[i] as iceAndFireSymbol;
            symbol.winBn = false;
            symbol.winLab.node.active = false;
        }
    }

    clear(): void {
        this.unscheduleAllCallbacks();
        super.clear();
    }
}

