import { _decorator, Component, macro, sp } from 'cc';
import { Node } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { Animation } from 'cc';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { labNumShow } from '../../module_basic/utils/labNumShow';
import { iceAndFire } from './cIceAndFire';
import { Tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('iceAndFireBigwin')
export class iceAndFireBigwin extends Component {

    @property({ type: labNumShow, tooltip: "coin" })
    public coinNum: labNumShow = null!;
    @property({ type: LanguageSprite, tooltip: "" })
    public bigWinBw: LanguageSprite = null!;

    private config: number[] = [15, 35];
    onLoad() {
        this.coinNum.setRealNum(0);
    }

    private _lev: number = 1;

    public get lev(): number {
        return this._lev;
    }

    public set lev(v: number) {
        if (this._lev != v) {
            if (v == 2) {
                this.bigWinBw.dataID = "mw";
            } else if (v == 3) {
                this.bigWinBw.dataID = "smw";
            }
            tween(this.bigWinBw.node)
                .to(0.5, { scale: new Vec3(1.2, 1.2) })
                .to(0.2, { scale: new Vec3(1, 1) })
                .start();
        }
        this._lev = v;
    }

    protected update(dt: number): void {
        let cur: number = this.coinNum.getRealNum();
        if (cur >= 15 && cur < 35) {
            this.lev = 2;
        } else if (cur >= 35) {
            this.lev = 3;
        }
    }

    private aim: number;
    playBigWin(aim: number) {
        iceAndFire.audio.playBigwin();
        this.aim = aim;
        let aimLev = 1;
        let t: number = 3;
        if (aim >= 15 && aim < 35) {
            aimLev = 2;
            t = 6;
        } else if (aim >= 35) {
            aimLev = 3;
            t = 9;
        }
        this.playOverBn = false;
        tween(this.coinNum)
            .to(t, { num: aim * 100 })
            .call(()=>{
                iceAndFire.audio.playBigwinEnd();
                this.playOverBn = true;
                this.scheduleOnce(this.closeBigWin,1);
            })
            .start();
    }

    private playOverBn = false;
    /**点击响应 */
    onFastBigWin(e: TouchEvent) {
        if (this.playOverBn) {
            this.closeBigWin();
        } else {
            this.playCoinOver();
        }
    }

    private closeBigWin() {
        this.unschedule(this.closeBigWin);
        iceAndFire.audio.stopBigwinEnd();
        iceAndFire.main.closeBigWin();
        this.node.destroy();
    }

    private playCoinOver() {
        Tween.stopAllByTarget(this.coinNum);
        this.coinNum.setRealNum(this.aim);
        iceAndFire.audio.playBigwinEnd();
        this.playOverBn = true;
        this.scheduleOnce(this.closeBigWin,1);
    }
}