import { _decorator, Component, Prefab, Node, Label } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Animation } from 'cc';
import { ESTATE, iceAndFire } from './cIceAndFire';
import { find } from 'cc';
import { macro } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('iceAndFireBg')
export class iceAndFireBg extends Component {
    @property({ type: Prefab, tooltip: "" })
    public coinNumPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public mulNumPb: Prefab = null!;
    @property({ type: Node, tooltip: "普通模式" })
    public normal: Node = null!;
    @property({ type: Node, tooltip: "财富模式" })
    public fortune: Node = null!;
    @property({ type: Node, tooltip: "" })
    public reelContent: Node = null!;
    @property({ type: Node, tooltip: "" })
    public reelTop: Node = null!;
    @property({ type: Node, tooltip: "" })
    public reelDown: Node = null!;
    @property({ type: Node, tooltip: "" })
    public redLight: Node = null!;
    @property({ type: Node, tooltip: "" })
    public bornEffect: Node = null!;
    /**当前模式 */
    private _mod: EMOD;
    public get mod(): EMOD {
        return this._mod
    }
    public set mod(v: EMOD) {
        if(v==EMOD.N) this.unscheduleAllCallbacks();
        this._mod = v;
        if(v == EMOD.N){
            iceAndFire.audio.playBgm(0);
        }else{
            iceAndFire.audio.playBgm(1);
        }
        this.normal.active = v == EMOD.N;
        this.fortune.active = v == EMOD.F;
        this.reelContent.opacity = v == EMOD.N ? 255 : 0;
        this.redLight.active = false;
        this.bornEffect.active = false;
        if (iceAndFire.slotCtrl && iceAndFire.slotCtrl.node) {
            iceAndFire.slotCtrl.rollPanel.active = v == EMOD.N;
            iceAndFire.slotCtrl.dataPanel.y = v == EMOD.N ? -396 : -632;
            iceAndFire.slotCtrl.free.active = v != EMOD.N;
        }
        if (iceAndFire.infoBoard) iceAndFire.infoBoard.mod = v;
    }


    onLoad() {
    }

    start() {
        this.mod = EMOD.N;
        iceAndFire.bg = this;
    }

    private get light(): Animation {
        return find("light", this.reelContent).getComponent(Animation);
    }
    startRollEffect() {
        if (this.mod == EMOD.N) {
            this.light.play();
        }
    }

    private startY: number = 190;
    private topAimY: number = 396;
    private downAimY: number = -190;
    private barTopAimY: number = 640;
    private barDownAimY: number = -432;
    private get waysBarTop(): Node {
        return find("waysBarTop", this.fortune);
    }
    private get waysBarDown(): Node {
        return find("waysBarDown", this.fortune);
    }
    private get waysBarTopLab(): Label {
        return find("Label", this.waysBarTop).getComponent(Label);
    }
    private get waysBarDownLab(): Label {
        return find("Label", this.waysBarDown).getComponent(Label);
    }
    private get bornEndAni(): Animation {
        return find("bornEnd", this.fortune).getComponent(Animation);
    }
    private get infoLoop(): Node {
        return find("infoLoop", this.fortune);
    }
    /**普通模式到财富模式过场 */
    public cutScenes() {
        this.clearScene();
        this.scheduleOnce(this.doCutScenes, 0.5);
    }

    private clearScene() {
        this.infoLoop.active = false;
        iceAndFire.infoBoard.node.active = false;
        this.reelTop.opacity = 0;
        this.reelDown.opacity = 0;
        this.reelContent.opacity = 255;
        find("boom", this.reelContent).active = true;
        this.redLight.scale = new Vec3(1, 3);
        find("bornFrame", this.reelDown).active = true;
        find("bornFrame", this.reelDown).active = true;
        find("frame", this.reelDown).active = false;
        find("frame", this.reelDown).active = false;
    }

    private doCutScenes() {
        iceAndFire.infoBoard.node.active = true;
        iceAndFire.infoBoard.node.opacity = 0;
        this.waysBarTop.active = true;
        this.waysBarTop.opacity = 0;
        this.waysBarTop.y = iceAndFire.infoBoard.node.y;
        this.waysBarDown.active = true;
        this.waysBarDown.opacity = 0;
        this.waysBarDown.y = iceAndFire.infoBoard.node.y;
        iceAndFire.topReel.bornLoop();
        iceAndFire.downReel.bornLoop();
        tween(this.reelContent)
            .to(0.6, { opacity: 0 })
            .call(() => {
                find("boom", this.reelContent).active = false;
            })
            .start();
        this.reelTop.y = this.startY;
        this.reelDown.y = this.startY;
        tween(this.reelDown)
            .to(0.6, { opacity: 255 })
            .call(() => {
                find("bornTop", this.reelDown).active = true;
                find("bornDown", this.reelDown).active = true;
                find("bornTop", this.reelTop).active = true;
                this.reelTop.opacity = 255;
                tween(this.reelDown)
                    .to(2, { y: this.downAimY })
                    .call(() => {
                        this.bornEndAni.play();
                    })
                    .start();
                tween(this.reelTop)
                    .to(2, { y: this.topAimY })
                    .start();

                this.waysBarTop.opacity = 255;
                this.waysBarTopLab.string = 2304 + "";
                this.waysBarDown.opacity = 255;
                this.waysBarDownLab.string = 576 + "";
                tween(this.waysBarDownLab)
                    .to(1, { string: 2304 + "" })
                    .call(() => {
                        tween(this.waysBarTop)
                            .to(1, { y: this.barTopAimY })
                            .start();
                        tween(this.waysBarDown)
                            .to(1, { y: this.barDownAimY })
                            .start();
                        tween(iceAndFire.infoBoard.node)
                            .to(1, { opacity: 255 })
                            .start();
                    })
                    .start();
            })
            .start();
        this.redLight.active = true;
        this.bornEffect.active = true;
        tween(this.redLight)
            .to(0.4, { scale: new Vec3(6, 6) })
            .to(0.1, { opacity: 0 })
            .call(() => {
                this.redLight.active = false;
                this.bornEffect.active = false;
                this.redLight.opacity = 200;
            })
            .start();
        this.scheduleOnce(() => {
            find("frame", this.reelDown).active = true;
            find("frame", this.reelDown).active = true;
            this.tweenHide(find("bornFrame", this.reelTop));
            this.tweenHide(find("bornFrame", this.reelDown));
            this.tweenHide(find("bornTop", this.reelDown));
            this.tweenHide(find("bornDown", this.reelDown));
            this.tweenHide(find("bornTop", this.reelTop));
            this.infoLoop.active = true;
            this.schedule(()=>{
                this.infoLoop.children[0].active = !this.infoLoop.children[0].active;
                this.infoLoop.children[1].active = !this.infoLoop.children[1].active;
            },3,macro.REPEAT_FOREVER);
        }, 2.5)
        this.scheduleOnce(() => {
            iceAndFire.main.doNext();
        },4);
    }

    private tweenHide(node: Node) {
        tween(node)
            .to(0.5, { opacity: 0 })
            .call(() => {
                node.active = false;
                node.opacity = 255;
            })
            .start();
    }
}

export enum EMOD {
    N,  //普通模式
    F   //财富模式
}


