import { Node } from "cc";
import { tween } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { Label } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";
import { iceAndFire } from "./cIceAndFire";
import { SpriteFrame } from "cc";
import { EMOD } from "./iceAndFireBg";
import { Sprite } from "cc";
import { find } from "cc";
import { lampUI } from "../../module_basic/utils/lampUI";
import { labNumShow } from "../../module_basic/utils/labNumShow";



const { ccclass, property } = _decorator;

@ccclass('iceAndFireInfoBoard')
export class iceAndFireInfoBoard extends Component {
    @property({ type: Sprite, tooltip: "背景" })
    public bg: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "普通模式背景" })
    public nBgs: SpriteFrame[] = [];
    @property({ type: [SpriteFrame], tooltip: "财富模式背景" })
    public fBgs: SpriteFrame[] = [];
    @property({ type: Node, tooltip: "消息" })
    public infos: Node = null!;
    @property({ type: Node, tooltip: "中奖金额" })
    public win: Node = null!;
    protected start(): void {
        this.mod = EMOD.N;
        this.winLev = EWinLev.a;
        this.state = EState.info;
        iceAndFire.infoBoard = this;
    }

    public get lab(): labNumShow {
        return find("Label", this.win).getComponent(labNumShow);
    }
    private get curWin(): Node {
        return find("info_win", this.win);
    }
    private get twWin(): Node {
        return find("info_tw", this.win);
    }

    /**显示赢得金币 */
    public playWinAni(coin: number, t: number = 0, twBn: boolean = false) {
        this.state = EState.win;
        this.bindLev(coin);
        this.curWin.active = !twBn;
        this.twWin.active = twBn;
        tween(this.lab)
            .to(t, { num: coin*100 })
            .start();
    }
    /**显示消息 */
    public showInfo() {
        this.state = EState.info;
    }

    private _winLev: number = EWinLev.a;
    public get winLev(): number {
        return this._winLev
    }
    public set winLev(v: number) {
        this._winLev = v;
        this.bg.spriteFrame = this.mod == EMOD.N ? this.nBgs[this.winLev] : this.fBgs[this.winLev];
    }

    /**获奖金额对应不同背景板 */
    private bindLev(coin: number) {
        if (coin > iceAndFire.betSum * 3 && coin < iceAndFire.betSum * 5) {
            this.winLev = EWinLev.b;
        } else if (coin >= iceAndFire.betSum * 5) {
            this.winLev = EWinLev.c;
        } else {
            this.winLev = EWinLev.a;
        }
    }

    /**当前模式 */
    private _mod: EMOD;
    public get mod(): EMOD {
        return this._mod
    }
    public set mod(v: EMOD) {
        if (this._mod == v) return;
        this._mod = v;
        if (this.node) this.node.y = v == EMOD.F ? 134 : -290;
        let arr: number[] = [];
        switch (v) {
            case EMOD.N:
                arr = [7, 8, 9]
                break;
            case EMOD.F:
                arr = [0, 1, 2, 3, 4, 5, 6]
                break;
            default:
                break;
        }
        if (this.infos) this.infos.getComponent(lampUI)?.setHideMessage(arr);
    }

    private _state: EState = EState.info;
    public get state(): EState {
        return this._state
    }
    public set state(v: EState) {
        this._state = v;
        this.infos.active = v == EState.info;
        this.win.active = v == EState.win;
        if (this.win.active == false) {
            this.lab.num = 0;
            this.winLev = EWinLev.a;
        }
    }
}

export enum EWinLev {
    "a" = 0,
    "b",
    "c",
}

export enum EState {
    "info" = 0,
    "win",
}
