import { _decorator, Component, Node, Prefab, sp, Sprite, SpriteFrame } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
import { Animation } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { winAni } from './winAni';
import { Label } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { iceAndFire } from './cIceAndFire';
const { ccclass, property } = _decorator;

@ccclass('iceAndFireSymbol')
export class iceAndFireSymbol extends baseSymbol {
    @property({ type: Animation, tooltip: "" })
    public winAni: Animation = null!;
    @property({ type: Animation, tooltip: "" })
    public wildWinEffectAni: Animation = null!;
    @property({ type: Label, tooltip: "" })
    public winLab: Label = null!;
    onLoad() {

    }

    protected start(): void {
        this.winAni.node.getComponent(winAni).callBack = () => { this.onWinAni() };
    }

    unuse(): void {
        super.unuse();
        this.winBn = false;
        this.winAni.node.active = false;
        this.wildWinEffectAni.node.active = false;
        this.winLab.node.active = false;
    }

    private onWinAni() {
        this.playAni(EROLEANI.win, false);
        this.addAni(EROLEANI.winidle, true);
    }

    winIdle() {
        this.playAni(EROLEANI.winidle, true);
    }

    private _winBn: boolean = false;
    public get winBn(): boolean {
        return this._winBn;
    }
    public set winBn(v: boolean) {
        this._winBn = v;
        this.winAni.node.active = v;
        if (v) {
            if (this.showType != ESHOWTYPE.ske) this.showType = ESHOWTYPE.ske;
            this.winAni.play();
        } else {
            this.winAni.stop();
        }
    }

    public get wildBn(): boolean {
        return this.symbolId == ESYMBOLID.wild;
    }

    /**wild 入场动画 */
    public wildSpawn() {
        if (this.symbolId != ESYMBOLID.wild) return;
        this.showType = ESHOWTYPE.ske;
        this.playAni(EROLEANI.spawn, false);
        this.addAni(EROLEANI.idle, true);
    }

    /**scatter 入场动画 */
    public scatterSpawn() {
        if (this.symbolId != ESYMBOLID.scatter) return;
        if (this.showType != ESHOWTYPE.ske) this.showType = ESHOWTYPE.ske;
        this.playAni(EROLEANI.spawn, false);
        this.addAni(EROLEANI.idle, true);
    }

    /**wild中奖红色烟雾 在任何主游戏旋转期间，任何赢奖涉及了1个或更多的百搭符号时，所有赢奖都将乘以2 */
    public playWildWinEffect() {
        if (this.wildWinEffectAni.node.active || this.symbolId != ESYMBOLID.wild) return;
        this.wildWinEffectAni.node.active = true;
        this.wildWinEffectAni.on("finished" as any, () => { this.wildWinEffectAni.node.active = false });
        this.wildWinEffectAni.play();
    }

    /**展示中奖金额 */
    showWin(coin: number) {
        this.winLab.node.active = true;
        this.winLab.string = Num.exchangeToThousands(iceAndFire.exchangeRate, coin);
    }

    onClick(e: TouchEvent) {
        iceAndFire.main.addDetail(this);
    }
}

export enum ESYMBOLID {
    null,
    diamond,
    club,
    heart,
    spade,
    clover,
    horseShoe,
    coinjar,
    chest,
    scatter,
    wild
}

export enum EROLEANI {
    win = "win",                  //中奖过场
    winidle = "win_idle",         //wild 中奖状态待机
    idle = "idle",                //普通待机
    spawn = "spawn",              //wild 入场动画
    fastspinExit = "fastspin_exit",
    fastspinIdle = "fastspin_idle",
    fastspinStart = "fastspin_start"
}