import { NodePool } from "cc";
import { _decorator } from "cc";
import { Vec3 } from "cc";
import { ETYPE, reelDate } from "../../module_basic/ui_slot/match3/baseReel";
import { baseSymbol } from "../../module_basic/ui_slot/match3/baseSymbol";
import { iceAndFire } from "./cIceAndFire";
import { iceAndFireReel } from "./iceAndFireReel";
import { CCInteger } from "cc";
const { ccclass, property } = _decorator;

@ccclass('iceAndFireFortuneReel')
export class iceAndFireFortuneReel extends iceAndFireReel {

    onLoad() {

    }

    start() {
        this.radomIds = [1, 2, 3, 4, 5, 6, 7, 8, 9];
        this.setReelDate();
        // this.scheduleOnce(this.loop, 1);
    }

    /**设置滚轴排布数据 */
    setReelDate() {
        this.reelDates = [
            { index: 0, type: ETYPE.V, num: 2, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(-313.65, -104.55), vector: 294.1, symbols: [], switch: false },
            { index: 1, type: ETYPE.V, num: 3, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(-209.1, -156.825), vector: 398.65, symbols: [], switch: false },
            { index: 2, type: ETYPE.V, num: 4, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(-104.55, -209.1), vector: 503.2, symbols: [], switch: false },
            { index: 3, type: ETYPE.V, num: 4, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(0, -209.1), vector: 503.2, symbols: [], switch: false },
            { index: 4, type: ETYPE.V, num: 4, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(104.55, -209.1), vector: 503.2, symbols: [], switch: false },
            { index: 5, type: ETYPE.V, num: 3, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(209.1, -156.825), vector: 398.65, symbols: [], switch: false },
            { index: 6, type: ETYPE.V, num: 2, pool: new NodePool(baseSymbol), size: 104.55, headPos: new Vec3(313.65, -104.55), vector: 294.1, symbols: [], switch: false },
        ]
    }

    bornLoop() {
        for (let i = 0; i < this.reelDates.length; i++) {
            this.moveOne(this.reelDates[i]);
        }
    }

    private disTime: number = 0.1;
    /**启动循环播放 */
    loop() {
        iceAndFire.audio.playReelLoop();
        if (iceAndFire.slotCtrl.isSpeed) {
            this.v = 60;
        } else {
            this.v = 40;
        }
        let disTime: number = iceAndFire.slotCtrl.isSpeed ? 0 : this.disTime;
        for (let i = 0; i < this.reelDates.length; i++) {
            this.scheduleOnce(() => {
                this.moveOne(this.reelDates[i]);
            }, disTime * i);
        }
    }

    /**停止循环展示结果 */
    loopEnd(arr: number[], callback: Function = () => { }) {
        if (iceAndFire.slotCtrl.isSpeed) {
            this.v = 60;
        } else {
            this.v = 40;
        }
        if (iceAndFire.slotCtrl.isSpeed == false) {
            for (let i = 0; i < this.reelDates.length; i++) {
                let r: reelDate = this.reelDates[i];
                let disTime: number = this.disTime * i;
                this.scheduleOnce(() => {
                    this.bindResultOne(r, arr, () => {
                        let cb: Function = () => {
                            iceAndFire.main.rollCallBack();
                            iceAndFire.audio.playReelEnd(false);
                        }
                        this.rebound(r, false, cb);
                    })
                }, disTime);
            }
        } else {
            this.bindResult(arr, () => {
                iceAndFire.main.rollCallBack();
                iceAndFire.audio.playReelEnd(true);
            });
        }
    }

}

export enum EREELTYPE {
    top,
    down
}
