import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('iceAndFireAudio')
export class iceAndFireAudio extends Component {
    onLoad() {
    }

    start() {
    }

    private urlArr: string[] = ["sound/bgm_mg", "sound/bgm_fs"];
    private bgmIndex: number;
    playBgm(index: number) {
        if (this.bgmIndex == index) return;
        this.bgmIndex = index;
        tgx.AudioMgr.inst.audio.stopMusic();
        tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[index], ModuleDef.SLOTS_ICEANDFIRE)
    }

    /**滚动开始 */
    playReelLoop() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinBtn, ModuleDef.SLOTS_ICEANDFIRE);
    }
    /**滚动结束 */
    private playReelEndBn: boolean = false;
    playReelEnd(isSpeed: boolean = false) {
        if (this.playReelEndBn) return;
        this.playReelEndBn = true
        this.scheduleOnce(() => {
            this.playReelEndBn = false;
        }, 0.01)
        if (isSpeed) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelStopQuick, ModuleDef.SLOTS_ICEANDFIRE);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelStopNormal, ModuleDef.SLOTS_ICEANDFIRE);
        }
    }
    /**spawn wild */
    playFly() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeFlyToInfobar, ModuleDef.SLOTS_ICEANDFIRE);
    }
    /**spawn wild */
    playSpawnWild() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symWild, ModuleDef.SLOTS_ICEANDFIRE);
    }
    /**spawn Scatter */
    playSpawnScatter() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symScatter, ModuleDef.SLOTS_ICEANDFIRE);
    }

    /**bigwin */
    private bigwinId: number;
    playBigwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_ICEANDFIRE).then((value) => {
            this.bigwinId = value;
        });
    }
    /**bigwin end */
    private bigwinEndId: number;
    playBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinId, EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_ICEANDFIRE);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_ICEANDFIRE).then((value) => {
            this.bigwinEndId = value;
        });
    }
    stopBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinEndId, EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_ICEANDFIRE);
    }
    /**totalwin end */
    private totalId: number;
    playTotalwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_totalwin_main, ModuleDef.SLOTS_ICEANDFIRE).then((value) => {
            this.totalId = value;
        });
    }
    /**totalwin end */
    private totalEndId: number;
    playTotalwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.totalId, EEFFECT.bgm_totalwin_main, ModuleDef.SLOTS_ICEANDFIRE);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_totalwin_end, ModuleDef.SLOTS_ICEANDFIRE).then((value) => {
            this.totalEndId = value;
        });
    }
    stopTotalwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.totalEndId, EEFFECT.bgm_totalwin_end, ModuleDef.SLOTS_ICEANDFIRE);
    }
}

export enum EEFFECT {
    fastspin = "sound/mainSfxGame_extract/fastspin",//夺宝等待动画
    fastspinScreenShakeLoop = "sound/mainSfxGame_extract/fastspinScreenShakeLoop",//夺宝地震
    prizeFlyToInfobar = "sound/sfxGame_extract/prizeFlyToInfobar",//飞
    prizeInfobarwinTotal = "sound/sfxGame_extract/prizeInfobarwinTotal",//共赢得
    spinBtn = "sound/sfxGame_extract/spinBtn",//快速循环滚动
    reelStopNormal = "sound/sfxGame_extract/reelStopNormal",//普通滚动停止
    reelStopQuick = "sound/sfxGame_extract/reelStopQuick",//快速滚动停止
    symWild = "sound/sfxGame_extract/symWild",//spawn wild
    symScatter = "sound/sfxGame_extract/symFs",//spawn scatter
    bgm_bigwin_main = "sound/bgm_bigwin_main",//bigwin
    bgm_bigwin_end = "sound/bgm_bigwin_end",//bigwin结束
    bgm_totalwin_main = "sound/bgm_totalwin_main",//共赢得
    bgm_totalwin_end = "sound/bgm_totalwin_end",//共赢得结束
}
