import { _decorator, Component, Prefab, Node, Label } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';

import { iceAndFire } from './cIceAndFire';

import { EMOD } from './iceAndFireBg';
import { labNumShow } from '../../module_basic/utils/labNumShow';
const { ccclass, property } = _decorator;

@ccclass('fortuneEnd')
export class fortuneEnd extends Component {
    @property({ type: Node, tooltip: "" })
    public content: Node = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    @property({ type: Label, tooltip: "" })
    public count: Label = null!;
    @property({ type: labNumShow, tooltip: "" })
    public winNum: labNumShow = null!;
    onLoad() {
    }

    start() {

    }

    show(count: number, coin: number) {
        this.content.scale = new Vec3(0, 0);
        tween(this.content)
            .to(0.5, { scale: new Vec3(1, 1) })
            .start();
        this.btn.active = false;
        this.count.string = count + "";
        this.winNum.num = 0;
        tween(this.winNum)
            .to(2, { num: coin * 100 })
            .call(() => {
                tween(this.content)
                    .to(0.2, { scale: new Vec3(1.2, 1.2) })
                    .to(0.2, { scale: new Vec3(1, 1) })
                    .start();
                this.btn.active = true;
                tween(this.btn)
                    .to(0.2, { scale: new Vec3(1.2, 1.2) })
                    .to(0.2, { scale: new Vec3(1, 1) })
                    .start();
            })
            .start();
        this.scheduleOnce(this.remove, 5);
    }

    remove() {
        iceAndFire.main.playRollOver();
        iceAndFire.bg.mod = EMOD.N;
        this.node.destroy();
    }

    onClik() {
        this.remove();
    }
}

